import {Injectable} from '@angular/core';
import {Http, Response, Headers, RequestOptions} from "@angular/http";
import {Observable} from 'rxjs/Rx';

@Injectable()
export class HomeService {

    constructor(private http:Http) {
    }


    get_appName()
    {
        let headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded','x-access-token':'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJVc2VySWQiOiJhZG1pbiIsImlhdCI6MTUzMjQxMjg1NSwiZXhwIjoxNTMyNDE2NDU1fQ.PK91PO2ZhA8apSbkfB_QAvF1Khh10pQRHKvmRSU0RrM'});  
        let body='tpass=Pass@123';
        let options = new RequestOptions({headers: headers});
        console.log(body);
        return this.http.post('http://localhost:9000/Login/admin', body, options).map((res:Response) =>  res.json()
    );
    }


    get_token()
    {
        let headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded','x-access-token':'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJVc2VySWQiOiJhZG1pbiIsImlhdCI6MTUzMjQxMjg1NSwiZXhwIjoxNTMyNDE2NDU1fQ.PK91PO2ZhA8apSbkfB_QAvF1Khh10pQRHKvmRSU0RrM'});  
        let body='tpass=Pass@123';
        let options = new RequestOptions({headers: headers});
        console.log(body);
        return this.http.post('http://localhost:9000/Login/admin', body, options).map((res:Response) =>  res.json()
    );
    }
   

    auth(userName,password,token) {
        console.log("Inside auth()");
        console.log("Token :"+token);
        let headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded','x-access-token':token});
        let options = new RequestOptions({headers: headers});
        //let body = JSON.stringify(cred);
        //let body = 'username=myusername&password=mypassword';
        let body='userName='+userName+'&password='+password;
        console.log(body);
        
        return this.http.post('http://localhost:4200/Login', body, options).map((res:Response) => res.json() 
    );
    }

   

   

}


