import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DoctorService } from './Doctor.service';

import 'rxjs/add/operator/toPromise';

@Component({
  selector: 'app-doctor',
  templateUrl: './Doctor.component.html',
  styleUrls: ['./Doctor.component.css'],
  providers: [DoctorService]
})
export class DoctorComponent implements OnInit {

  myForm: FormGroup;

  private allParticipants;
  private mydoctors;
  public alldoctors=[{doctorId:'824',fullName:'Jacob',address:'address1',Specialisation:'my_spec1',accessallowed:true},{doctorId:'825',fullName:'Rajeev',address:'address2',Specialisation:'my_spec2',accessallowed:true},{doctorId:'826',fullName:'Emanuel',address:'address3',Specialisation:'my_spec3',accessallowed:false},{doctorId:'827',fullName:'Mathew',address:'address4',Specialisation:'my_spec4',accessallowed:false}];
  public allowed_doctors=[];
  private filtered_data;
  private participant;
  private currentId;
  private errorMessage;
  public flag_filter=true;

  doctorId = new FormControl('', Validators.required);
  fullName = new FormControl('', Validators.required);
  registrationNumber = new FormControl('', Validators.required);
  Specialisation = new FormControl('', Validators.required);
  address = new FormControl('', Validators.required);
  myPatients = new FormControl('', Validators.required);


  constructor(public serviceDoctor: DoctorService, fb: FormBuilder) {
    this.flag_filter=true;
    this.myForm = fb.group({
      doctorId: this.doctorId,
      fullName: this.fullName,
      registrationNumber: this.registrationNumber,
      Specialisation: this.Specialisation,
      address: this.address,
      myPatients: this.myPatients
    });
    var i;
    for(i=0;i<this.alldoctors.length;i++)
    {
      if(this.alldoctors[i].accessallowed==true)
      {
        this.allowed_doctors.push(this.alldoctors[i]);
      }
    }
  };

  ngOnInit(): void {
    // localStorage.setItem('id','resource%3Aayush.Patient%23P1');
    localStorage.setItem('recordId',localStorage.getItem('id'));
    // localStorage.setItem('doctorId','resource:ayush.Doctor#D2');
    this.loadAll();
    this.getFiltered('resource%3Aayush.Patient%23'+(localStorage.getItem('id')));
  }

  loadAll(): Promise<any> {
    const tempList = [];
    return this.serviceDoctor.getAll()
    .toPromise()
    .then((result) => {
      this.errorMessage = null;
      result.forEach(participant => {
        tempList.push(participant);
      });
      this.allParticipants = tempList;
      console.log('All Doctors ');
      console.log(this.allParticipants);
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
        this.errorMessage = error;
      }
    });
  }


  getFiltered(id:any): Promise<any> {
    const tempList = [];
    return this.serviceDoctor.getFiltered(id)
    .toPromise()
    .then((result) => {
      this.errorMessage = null;
      result.forEach(participant => {
        tempList.push(participant);
      });
      this.mydoctors = tempList;
      console.log('My Doctors ');
      console.log(this.mydoctors);
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
        this.errorMessage = error;
      }
    });
  }

	/**
   * Event handler for changing the checked state of a checkbox (handles array enumeration values)
   * @param {String} name - the name of the participant field to update
   * @param {any} value - the enumeration value for which to toggle the checked state
   */
  changeArrayValue(name: string, value: any): void {
    const index = this[name].value.indexOf(value);
    if (index === -1) {
      this[name].value.push(value);
    } else {
      this[name].value.splice(index, 1);
    }
  }

	/**
	 * Checkbox helper, determining whether an enumeration value should be selected or not (for array enumeration values
   * only). This is used for checkboxes in the participant updateDialog.
   * @param {String} name - the name of the participant field to check
   * @param {any} value - the enumeration value to check for
   * @return {Boolean} whether the specified participant field contains the provided value
   */
  hasArrayValue(name: string, value: any): boolean {
    return this[name].value.indexOf(value) !== -1;
  }

  addParticipant(form: any): Promise<any> {
    this.participant = {
      $class: 'ayush.Doctor',
      'doctorId': this.doctorId.value,
      'fullName': this.fullName.value,
      'registrationNumber': this.registrationNumber.value,
      'Specialisation': this.Specialisation.value,
      'address': this.address.value,
      'myPatients': this.myPatients.value
    };

    this.myForm.setValue({
      'doctorId': null,
      'fullName': null,
      'registrationNumber': null,
      'Specialisation': null,
      'address': null,
      'myPatients': null
    });

    return this.serviceDoctor.addParticipant(this.participant)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.myForm.setValue({
        'doctorId': null,
        'fullName': null,
        'registrationNumber': null,
        'Specialisation': null,
        'address': null,
        'myPatients': null
      });
      this.loadAll();
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else {
        this.errorMessage = error;
      }
    });
  }


  grantAccess(doctorId): Promise<any> {
    this.participant = {
      $class: 'ayush.GrantAccess',
      'authorisedToModify': doctorId,
      'medicalRecord': localStorage.getItem('recordId')
        };

    return this.serviceDoctor.grantAccess(this.participant)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.loadAll();
      this.getFiltered('resource%3Aayush.Patient%23'+(localStorage.getItem('id')));
      alert('Granted the Access to the Record');
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else {
        this.errorMessage = error;
      }
    });
  }

  revokeAccess(doctorId): Promise<any> {
    this.participant = {
      $class: 'ayush.revokeAccess',
      'revokeThisDoctor': doctorId,
      'medicalRecord': localStorage.getItem('recordId')
        };

    return this.serviceDoctor.revokeAccess(this.participant)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.loadAll();
      this.getFiltered('resource%3Aayush.Patient%23'+(localStorage.getItem('id')));
      alert('Access Revoked');
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else {
        this.errorMessage = error;
      }
    });
  }



   updateParticipant(form: any): Promise<any> {
    this.participant = {
      $class: 'ayush.Doctor',
      'fullName': this.fullName.value,
      'registrationNumber': this.registrationNumber.value,
      'Specialisation': this.Specialisation.value,
      'address': this.address.value,
      'myPatients': this.myPatients.value
    };

    return this.serviceDoctor.updateParticipant(form.get('doctorId').value, this.participant)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.loadAll();
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
      } else {
        this.errorMessage = error;
      }
    });
  }


  deleteParticipant(): Promise<any> {

    return this.serviceDoctor.deleteParticipant(this.currentId)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.loadAll();
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
      } else {
        this.errorMessage = error;
      }
    });
  }

  setId(id: any): void {
    this.currentId = id;
  }

  getForm(id: any): Promise<any> {

    return this.serviceDoctor.getparticipant(id)
    .toPromise()
    .then((result) => {
      this.errorMessage = null;
      const formObject = {
        'doctorId': null,
        'fullName': null,
        'registrationNumber': null,
        'Specialisation': null,
        'address': null,
        'myPatients': null
      };

      if (result.doctorId) {
        formObject.doctorId = result.doctorId;
      } else {
        formObject.doctorId = null;
      }

      if (result.fullName) {
        formObject.fullName = result.fullName;
      } else {
        formObject.fullName = null;
      }

      if (result.registrationNumber) {
        formObject.registrationNumber = result.registrationNumber;
      } else {
        formObject.registrationNumber = null;
      }

      if (result.Specialisation) {
        formObject.Specialisation = result.Specialisation;
      } else {
        formObject.Specialisation = null;
      }

      if (result.address) {
        formObject.address = result.address;
      } else {
        formObject.address = null;
      }

      if (result.myPatients) {
        formObject.myPatients = result.myPatients;
      } else {
        formObject.myPatients = null;
      }

      this.myForm.setValue(formObject);
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
      } else {
        this.errorMessage = error;
      }
    });

  }
  filterdata(): void{
    if(this.flag_filter)
    {
      this.flag_filter=false;
    }
    else
    {
      this.flag_filter=true;
    }
  }

  resetForm(): void {
    this.myForm.setValue({
      'doctorId': null,
      'fullName': null,
      'registrationNumber': null,
      'Specialisation': null,
      'address': null,
      'myPatients': null
    });
  }
}



