import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

//import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';

import { MedicalRecordComponent } from './MedicalRecord/MedicalRecord.component';

import { PatientComponent } from './Patient/Patient.component';
import { DoctorComponent } from './Doctor/Doctor.component';
import { HospitalComponent } from './Hospital/Hospital.component';

import { GrantAccessComponent } from './GrantAccess/GrantAccess.component';
import { revokeAccessComponent } from './revokeAccess/revokeAccess.component';
import { GrantAccessToHospitalComponent } from './GrantAccessToHospital/GrantAccessToHospital.component';
import { revokeAccessFromHospitalComponent } from './revokeAccessFromHospital/revokeAccessFromHospital.component';
import { CreateMedicalRecordComponent } from './CreateMedicalRecord/CreateMedicalRecord.component';
import { getUserTypeComponent } from './getUserType/getUserType.component';

const routes: Routes = [
  { path: '', component: LoginComponent },
  { path: 'MedicalRecord', component: MedicalRecordComponent },
  { path: 'Patient', component: PatientComponent },
  { path: 'Doctor', component: DoctorComponent },
  { path: 'Hospital', component: HospitalComponent },
  { path: 'GrantAccess', component: GrantAccessComponent },
  { path: 'revokeAccess', component: revokeAccessComponent },
  { path: 'GrantAccessToHospital', component: GrantAccessToHospitalComponent },
  { path: 'revokeAccessFromHospital', component: revokeAccessFromHospitalComponent },
  { path: 'CreateMedicalRecord', component: CreateMedicalRecordComponent },
  { path: 'Login',component: LoginComponent},
  { path: 'getUserType', component: getUserTypeComponent },
  { path: '**', redirectTo: '' }
];

@NgModule({
 imports: [RouterModule.forRoot(routes)],
 exports: [RouterModule],
 providers: []
})
export class AppRoutingModule { }



