import { Injectable } from '@angular/core';
import { DataService } from '../data.service';
import { Observable } from 'rxjs/Observable';
import { MedicalRecord } from '../ayush';
import 'rxjs/Rx';

// Can be injected into a constructor
@Injectable()
export class MedicalRecordService {

  private NAMESPACE = 'MedicalRecord';

  constructor(private dataService: DataService<MedicalRecord>) {
  };

  public getAll(): Observable<MedicalRecord[]> {
    return this.dataService.getAll(this.NAMESPACE);
  }

  public getMedDoc(id: any): Observable<MedicalRecord[]> {
    return this.dataService.getMedDoc(id);
  }

  public getAsset(id: any): Observable<MedicalRecord> {
    return this.dataService.getSingle(this.NAMESPACE, id);
  }

  public addAsset(itemToAdd: any): Observable<MedicalRecord> {
    return this.dataService.add(this.NAMESPACE, itemToAdd);
  }

  public updateAsset(id: any, itemToUpdate: any): Observable<MedicalRecord> {
    return this.dataService.update(this.NAMESPACE, id, itemToUpdate);
  }

  public modifyRecord(itemToAdd: any): Observable<MedicalRecord> {
    return this.dataService.add('ModifyMedicalRecordDoctor', itemToAdd);
  }

  public deleteAsset(id: any): Observable<MedicalRecord> {
    return this.dataService.delete(this.NAMESPACE, id);
  }

  public getFiltered(id:any): Observable<MedicalRecord[]> {
    return this.dataService.getRecDoc(id,'getMedicalRecordDoctor');
  }

}



