import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { MedicalRecordService } from './MedicalRecord.service';
import 'rxjs/add/operator/toPromise';

@Component({
  selector: 'app-medicalrecord',
  templateUrl: './MedicalRecord.component.html',
  styleUrls: ['./MedicalRecord.component.css'],
  providers: [MedicalRecordService]
})
export class MedicalRecordComponent implements OnInit {

  myForm: FormGroup;
  search_name:string="";
  public found=[];
  public allpatients=[{patientid:'123',name:'new_1',address:'newaddr'},{patientid:'234',name:'new_2',address:'newaddr1'},{patientid:'345',name:'new_3',address:'newaddr2'},{patientid:'456',name:'new_4',address:'newaddr3'}];
  public flag_filter=false;
  private allAssets;
  private asset;
  private currentId;
  private errorMessage;

  recordId = new FormControl('', Validators.required);
  medicalHistory = new FormControl('', Validators.required);
  symptoms = new FormControl('', Validators.required);
  currentMedication = new FormControl('', Validators.required);
  owner = new FormControl('', Validators.required);
  authorisedDoctors = new FormControl('', Validators.required);
  authorisedHospitals = new FormControl('', Validators.required);

  constructor(public serviceMedicalRecord: MedicalRecordService, fb: FormBuilder) {
    this.myForm = fb.group({

      recordId: this.recordId,
      medicalHistory: this.medicalHistory,
      symptoms: this.symptoms,
      currentMedication: this.currentMedication,
      owner: this.owner,
      authorisedDoctors: this.authorisedDoctors,
      authorisedHospitals: this.authorisedHospitals
    });
  };

  ngOnInit(): void {
    this.loadAll('resource%3Aayush.Doctor%23'+(localStorage.getItem('id')));
  }

  loadAll(id: any): Promise<any> {
    console.log("Fn triggered");
    console.log(id);
    const tempList = [];
    return this.serviceMedicalRecord.getFiltered(id)
    .toPromise()
    .then((result) => {
      this.errorMessage = null;
      result.forEach(asset => {
        tempList.push(asset);
      });
      this.allAssets = tempList;
      console.log(this.allAssets);
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
      } else {
        this.errorMessage = error;
      }
    });
  }

	/**
   * Event handler for changing the checked state of a checkbox (handles array enumeration values)
   * @param {String} name - the name of the asset field to update
   * @param {any} value - the enumeration value for which to toggle the checked state
   */
  changeArrayValue(name: string, value: any): void {
    const index = this[name].value.indexOf(value);
    if (index === -1) {
      this[name].value.push(value);
    } else {
      this[name].value.splice(index, 1);
    }
  }

	/**
	 * Checkbox helper, determining whether an enumeration value should be selected or not (for array enumeration values
   * only). This is used for checkboxes in the asset updateDialog.
   * @param {String} name - the name of the asset field to check
   * @param {any} value - the enumeration value to check for
   * @return {Boolean} whether the specified asset field contains the provided value
   */
  hasArrayValue(name: string, value: any): boolean {
    return this[name].value.indexOf(value) !== -1;
  }

  addAsset(form: any): Promise<any> {
    this.asset = {
      $class: 'ayush.MedicalRecord',
      'recordId': this.recordId.value,
      'medicalHistory': this.medicalHistory.value,
      'symptoms': this.symptoms.value,
      'currentMedication': this.currentMedication.value,
      'owner': this.owner.value,
      'authorisedDoctors': this.authorisedDoctors.value,
      'authorisedHospitals': this.authorisedHospitals.value
    };

    this.myForm.setValue({
      'recordId': null,
      'medicalHistory': null,
      'symptoms': null,
      'currentMedication': null,
      'owner': null,
      'authorisedDoctors': null,
      'authorisedHospitals': null
    });

    return this.serviceMedicalRecord.addAsset(this.asset)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.myForm.setValue({
        'recordId': null,
        'medicalHistory': null,
        'symptoms': null,
        'currentMedication': null,
        'owner': null,
        'authorisedDoctors': null,
        'authorisedHospitals': null
      });
      this.loadAll('resource%3Aayush.Doctor%23'+(localStorage.getItem('id')));
    })
    .catch((error) => {
      if (error === 'Server error') {
          this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else {
          this.errorMessage = error;
      }
    });
  }


  


  modify(form:any): Promise<any> {
    this.asset = {
      $class: 'ayush.ModifyMedicalRecordDoctor',
      'medicalHistory': this.medicalHistory.value,
      'symptoms': this.symptoms.value,
      'currentMedication': this.currentMedication.value,
      'medicalRecord' :this.recordId.value,
      'doctor': localStorage.getItem('id')
        };
        console.log(this.asset);

    return this.serviceMedicalRecord.modifyRecord(this.asset)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      alert('Record Modified');
      this.loadAll('resource%3Aayush.Doctor%23'+(localStorage.getItem('id')));
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else {
        this.errorMessage = error;
      }
    });
  }


  deleteAsset(): Promise<any> {

    return this.serviceMedicalRecord.deleteAsset(this.currentId)
    .toPromise()
    .then(() => {
      this.errorMessage = null;
      this.loadAll('resource%3Aayush.Doctor%23'+(localStorage.getItem('id')));
    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
      } else {
        this.errorMessage = error;
      }
    });
  }

  setId(id: any): void {
    this.currentId = id;
  }

  getForm(id: any): Promise<any> {

    return this.serviceMedicalRecord.getAsset(id)
    .toPromise()
    .then((result) => {
      this.errorMessage = null;
      const formObject = {
        'recordId': null,
        'medicalHistory': null,
        'symptoms': null,
        'currentMedication': null,
        'owner': null,
        'authorisedDoctors': null,
        'authorisedHospitals': null
      };

      if (result.recordId) {
        formObject.recordId = result.recordId;
      } else {
        formObject.recordId = null;
      }

      if (result.medicalHistory) {
        formObject.medicalHistory = result.medicalHistory;
      } else {
        formObject.medicalHistory = null;
      }

      if (result.symptoms) {
        formObject.symptoms = result.symptoms;
      } else {
        formObject.symptoms = null;
      }

      if (result.currentMedication) {
        formObject.currentMedication = result.currentMedication;
      } else {
        formObject.currentMedication = null;
      }

      if (result.owner) {
        formObject.owner = result.owner;
      } else {
        formObject.owner = null;
      }

      if (result.authorisedDoctors) {
        formObject.authorisedDoctors = result.authorisedDoctors;
      } else {
        formObject.authorisedDoctors = null;
      }

      if (result.authorisedHospitals) {
        formObject.authorisedHospitals = result.authorisedHospitals;
      } else {
        formObject.authorisedHospitals = null;
      }

      this.myForm.setValue(formObject);

    })
    .catch((error) => {
      if (error === 'Server error') {
        this.errorMessage = 'Could not connect to REST server. Please check your configuration details';
      } else if (error === '404 - Not Found') {
        this.errorMessage = '404 - Could not find API route. Please check your available APIs.';
      } else {
        this.errorMessage = error;
      }
    });
  }

  showall()
  {
    document.getElementById('window1').style.display='none';
    document.getElementById('window').style.display='block';
    document.getElementById('backbutton').style.display='none';
    this.flag_filter=false;
    this.found=[]
  }

  search()
  {
    this.found=[];
    document.getElementById('window').style.display='none';
    document.getElementById('window1').style.display='block';
    document.getElementById('backbutton').style.display='block';
    var name=this.search_name;
    this.found=[];
    var i;
    var obj=this.allpatients;
      for(i=0;i<obj.length;i++)
      {
      	if(name==obj[i].name)
    		  this.found.push(obj[i]);
      }
      console.log(this.found);
      if(this.found.length!=0)
        document.getElementById('window1').innerHTML="<div *ngFor='let participant of found style='background-color:#d8d8d8;border-radius:10px;display:inline-block;margin:10px;'><div class='hos' style='width:200px;border-radius:10px;'><img src='https://butterflytronics.eu/wp-content/themes/twentyseventeen-child/_icones/Documents/BT_file_2_notepad.png' style='padding:10px;height:150px;width:190px;border-radius:10px;'><div style='padding:4px;margin:3px;text-align: center;'><b><p>Id : "+this.found[0].patientid+"</p><p>Name : "+this.found[0].name+"</p><p>Address : "+this.found[0].address+"</p> <p><button (click)='getForm(participant.patientid);' data-toggle='modal' data-target='#updateAssetModal'  style='border: none;color: white;padding: 20px;text-align: center;text-decoration: none;display: inline-block;font-size: 16px;margin: 4px 2px;cursor: pointer;background-color: #ff471a;border-radius:10px;'>Update Record</button></p></b></div></div></div>";
      else
      document.getElementById('window1').innerHTML="";
  }



  




  
  resetForm(): void {
    this.myForm.setValue({
      'recordId': null,
      'medicalHistory': null,
      'symptoms': null,
      'currentMedication': null,
      'owner': null,
      'authorisedDoctors': null,
      'authorisedHospitals': null
      });
  }

}



